<template>
    <div>
        <div id="amap" class="amap-style"></div>
        <div class="top-wrapper">
            <div class="content">
                <div class="btn-wrapper" @click="$router.go(-1)">
                    <i class="el-icon-arrow-left" style="color: white"></i>
                </div>
<!--                <div class="btn-wrapper">-->
<!--                    <i class="el-icon-more" style="color: white;"></i>-->
<!--                </div>-->
            </div>
        </div>
        <div class="bottom-wrapper">
            <div class="content">
                <div class="bottom-left">
                    <div class="title">{{name}}</div>
                    <div class="address">{{address}}</div>
                </div>
                <div class="bottom-right" @click="toApp">
                    <svg t="1639707816344" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="2067" width="32" height="32"><path d="M512 512m-512 0a512 512 0 1 0 1024 0 512 512 0 1 0-1024 0Z" fill="#27BB07" p-id="2068"></path><path d="M242.107411 512m7.240773-7.240773l255.411043-255.411043q7.240773-7.240773 14.481546 0l255.411043 255.411043q7.240773 7.240773 0 14.481546l-255.411043 255.411043q-7.240773 7.240773-14.481546 0l-255.411043-255.411043q-7.240773-7.240773 0-14.481546Z" fill="#FFFFFF" p-id="2069"></path><path d="M532.48 469.98528v54.8352h68.7104v30.72H532.48V645.12h-30.72V469.98528h-35.55328a10.24 10.24 0 0 1-7.14752-17.58208l53.20704-51.79392a10.24 10.24 0 0 1 14.2848 0l53.1968 51.79392a10.24 10.24 0 0 1-7.14752 17.58208H532.48z" fill="#09BB07" p-id="2070"></path></svg>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "mapComp",
        data() {
            return {
                gaodeMap: '',
                name: '广州市胸科医院',
                address: '广州市桂英路62号',
                lng: [117.619585, 36.299379],
                marker: ''
            }
        },
        mounted() {
            let name = this.$route.query.name
            let lng = this.$route.query.lng // '120.619585, 31.299379'
            let address = this.$route.query.address
            if(name) this.name = name
            if(lng) this.lng = lng
            if(address) this.address = address
            this.init()
        },
        methods: {
            init() {
                let that = this
                console.log('地图初始化前');
                this.gaodeMap = new AMap.Map('amap', {
                    resizeEnable: true, //是否监控地图容器尺寸变化
                    zoom: 14, //初始化地图层级
                    center: this.lng //初始化地图中心点
                });
                // 定位组件
                // AMap.plugin('AMap.Geolocation', function () {
                //     console.log(`定位组件`)
                //     let geolocation = new AMap.Geolocation({
                //         enableHighAccuracy: true,
                //         timeout: 10000,
                //         buttonPosition: 'RB',
                //         buttonOffset: new AMap.Pixel(10, 40),
                //         zoomToAccuracy: false,
                //
                //     });
                //     that.gaodeMap.addControl(geolocation);
                //     geolocation.getCurrentPosition(function (status, result) {
                //         if (status == 'complete') {
                //             console.log(`定位成功`)
                //         } else {
                //             console.error(`定位失败`, status, result)
                //         }
                //     });
                // });
                // 添加标记并居中
                this.marker = new AMap.Marker({
                    icon: "//a.amap.com/jsapi_demos/static/demo-center/icons/poi-marker-default.png",
                    // content: `<img src="http://47.94.6.131:8081/图片/marker.png" />`,
                    // content: `<img src="~@/assets/marker.png" />`,
                    position: this.lng,
                    offset: new AMap.Pixel(-13, -30)
                });
                this.marker.setMap(this.gaodeMap);
                this.gaodeMap.setFitView()
                console.log('wl-AMap:', this.gaodeMap);
            },
            toApp() {
                const uAgent = navigator.userAgent;
                const isAndroid = uAgent.indexOf('Android') > -1 || uAgent.indexOf('Linux') > -1;
                const isIos = uAgent.indexOf('iPhone') > -1;
                let href;
                setTimeout( () => {
                    if (document.hidden || document.webkitHidden) return ;
                    href = `https://uri.amap.com/marker?position=${this.lng.join(',')}`;
                    this.callLink(href)
                },3000);

                if (isAndroid) {
                    href = `androidamap://viewMap?sourceApplication=appname&poiname=${this.name}&lon=${this.lng[0]}&lat=${this.lng[1]}&dev=0`;
                    this.callLink(href);
                }
                if (isIos) {
                    href = `iosamap://viewMap?sourceApplication=applicationName&poiname=${this.name}&lon=${this.lng[0]}&lat=${this.lng[1]}&dev=1`;
                    this.callLink(href)
                }
            },
            callLink(href) {
                const a = document.createElement('a');
                a.href = href;
                a.style.display = 'none';
                document.body.appendChild(a);
                a.click();
                document.body.removeChild(a);
            }
        }
    }
</script>

<style lang="scss" scoped>
    .amap-style {
        width: 100vw;
        height: calc(100vh - 80px);
        z-index: 100;
        /deep/.amap-geolocation-con .amap-geo {
            width: 40px;
            height: 40px;
            border-radius: 20px;
            background: #fffffe url('~@/assets/position.png') no-repeat center;
            &:active {
                background: rgba(227,227,227, 0.5) url('~@/assets/position.png') no-repeat center;
            }
        }
    }
    .top-wrapper {
        z-index: 9999;
        width: 100vw;
        position: absolute;
        top: 0;
        /*background-color: #3a8ee6;*/
        .content {
            display: flex;
            justify-content: space-between;
            padding: 10px 10px 0 10px;
            .btn-wrapper {
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 5px;
                width: 40px;
                height: 40px;
                background-color: #9D9895;
            }
        }
    }

    .bottom-wrapper {
        z-index: 9999;
        width: 100vw;
        position: absolute;
        bottom: 0;
        height: 100px;
        background-color: white;
        .content {
            display: flex;
            justify-content: space-between;
            align-items: center;
            height: 100%;
            padding: 0 20px 0 20px;
            .bottom-left {
                .title {
                    font-size: 19px;
                    font-weight: 500;
                }
                .address {
                    font-size: 12px;
                    margin-top: 5px;
                    color: gray;
                }
            }
            .bottom-right {

            }
        }
    }
</style>