import axios from 'axios'
import {Toast} from "vant"
let wx = {};
wx.request = (params) => {
    axios({method: params.method, url: params.url, data: params.data}).then(res => {
        if(!res || (res && !res.data.data)){
            Toast('系统繁忙请稍后再试')
            params.fail()
            return
        }
        if(res && res.data && res.data.data && res.data.data.answer && res.data.data.answer.exception){
            console.log(res.data.data.answer.reply)
            // Toast(res.data.data.answer.reply)
            Toast('系统繁忙请稍后再试')
            params.fail()
            return
        }
        if(res.message) {
            params.fail()
            Toast('页面开小差，请重新导诊')
        }
        params.success(res)
    }).catch(err => {
        params.fail(err)
        switch(err.response && err.response.status){
            case 400: Toast('页面开小差，请重新导诊'); break;
            case 500: Toast(err.message); break;
            default: Toast('系统繁忙请稍后再试'); break;
        }
    });
}
export default wx
