<template>
  <div id="home" class="home-layout">
    <div class="home-header-content">
      <div class="home-header">
        <img class="doctor" src="../static/doctor.svg">
        <div class="title">嗨，我是AI导诊助手</div>
        <div class="refresh" @click="refresh">
          重新导诊
          <!--          <img src="../static/refresh.png">-->
        </div>
        <div :style="data.clientWidth < 350 ? 'width: 50%': ''" class="select" @click="showModel">
          <span :style="data.selectName == '请选择就诊对象' ? 'color:#CFD2D4':''">{{ data.selectName }}</span>
          <van-icon class="icon-select" name="play"/>
          <!-- 功能提示框 -->
        </div>
      </div>
    </div>
    <div :scroll-y="true"
         :style="`height: calc(100vh - ${(data.show || data.show2) && data.bottomHeight || data.viewHeight ? data.bottomHeight || data.viewHeight: '150'}px)`"
         class="home-center"
         id="contain">
      <!-- margintop会强制滚动 设置一个div撑开-->
      <div style="height: 30px;"></div>
      <template v-for="(item, index) of data.msgList">
        <div v-if="item.type === 0"
             :style="{marginLeft:  + (index === data.msgList.length - 1 && data.sendStatus ? data.viewLeft:'') + 'px'}"
             class="msg-left">
          <span>{{ item.msg }}</span>
        </div>
        <div v-if="item.type === 1"
             :style="{marginRight:  (index === data.msgList.length - 1 && data.sendStatus ?data.viewRight:'') + 'px'}"
             class="msg-right">
          <div class="content">{{ item.msg }}</div>
          <div class="user-img">
            <img :src="require('../static/user.svg')">
            <!-- <open-data type="userAvatarUrl"></open-data> -->
          </div>
        </div>
        <div v-if="item.type === 2 "
             :style="{marginLeft:  + (index === data.msgList.length - 1 && data.sendStatus ? data.viewLeft:'') + 'px'}"
             class="msg-left-other">
          <div class="txt-other">{{ item.msg }}</div>
          <div class="other-list">
            <template v-for="(i, index) of item.list">
              <!--              <div class="goto">-->
              <!--                <div>{{ i.name }}</div>-->
              <!--                <div @click="registered(i.name)" class="goto-part">挂号</div>-->
              <!--              </div>-->
              <div class="goto">
                <img src="../static/deptGo.svg">
                <div>{{ i.name }}</div>
                <div @click="registered(i.name)" class="goto-part">挂号</div>
              </div>
            </template>
          </div>
          <div class="evaluation">
            <div v-if="item.isup === true || item.isup == null" :class="item.isup?'up':'down'"
                 @click="setUporDown(index, 1)">
              <img :src="require(item.isup?'../static/up.png':'../static/upGary.png')">
              <span> 准确</span>
            </div>
            <div v-if="item.isup === false || item.isup == null"
                 :class="item.isup || item.isup === null?'down':'up'"
                 @click="setUporDown(index, 0)">
              <img :src="require(item.isup || item.isup === null?'../static/down.png':'../static/downLight.png')">
              <span> 不准确</span>
            </div>
          </div>
        </div>
      </template>
    </div>
<!--    v-if="!data.show && !data.show2"-->
    <div class="home-footer" :style="`bottom: ${data.viewHeight}px`">
      <el-input v-model="data.msgValue"
                @keyup.enter.native="msgSelect"
                @blur="inputblur"
                @focus="inputFocus"
                id="inputs"
                class="input"
                placeholder-style="color:rgba(50, 50, 51, 0.5);font-size:28rpx;"
                name="userName"
                placeholder="请描述您的症状，例如：最近经常头晕"/>
      <img class="block-key" @click="msgSelect" src="../static/send.svg">
    </div>

    <!-- 消息loading -->
    <div v-if="data.loading" class="msg-loading">
      <div class="one"></div>
      <div class="two"></div>
      <div class="three"></div>
    </div>
    <!-- 弹出框 -->
    <van-popup v-model="data.show" :overlay="false" position="bottom">
      <!--      <van-picker id="picker" :loading="data.noClike || data.loadingAll" v-if="data.status === 1 && data.columnsSign"-->
      <!--                  show-toolbar-->
      <!--                  title="请选择就诊对象"-->
      <!--                  :columns="data.localMsg"-->
      <!--                  @cancel="onClose"-->
      <!--                  @confirm="onConfirm"/>-->


      <div id="picker" v-if="data.status === 1 && data.columnsSign">
        <div class="special-title">请选择就诊对象</div>
        <div class="special-model">
          <van-picker id="picker" :loading="data.noClike || data.loadingAll" :columns="data.localMsg " ref="picker"/>
        </div>
        <div v-if="data.initUser" style="position:relative;height: 120px;">
          <div class="submit2 special-ms" @click="onConfirm">确定</div>
        </div>
        <div v-if="!data.initUser" style="position:relative;height: 120px;">
          <div class="user-two">
            <div @click="onClose">取消</div>
            <div @click="onConfirm">确定</div>
          </div>
        </div>
      </div>

      <!--      <div v-if="data.status === 1 && !data.columnsSign">-->
      <!--        <div class="special-model">-->
      <!--          <van-picker id="picker"-->
      <!--                      :loading="data.noClike || data.loadingAll"-->
      <!--                      :columns="data.oneColumns "-->
      <!--                      ref="picker"-->
      <!--                      default-index="2"-->
      <!--                      @cancel="onClose"-->
      <!--                      @confirm="onConfirm"/>-->
      <!--        </div>-->
      <!--        <div style="position:relative;height: 120px;">-->
      <!--          <div class="submit special-ms" @click="submitOne">确定</div>-->
      <!--        </div>-->
      <!--      </div>-->

      <div id="picker2" v-if=" data.status === 2" class="model-parts">
        <div v-if="data.partsList && data.partsList.length > 0" scroll-y="true" class="model-contain">
          <template v-for="(item, index) of data.partsList">
            <div @click="moreSelect(index)"  :class="item.select?'block-list-select block-list':'block-list'">
<!--              :class=" item.name && item.name.length > 5 ? 'gdView' :''"-->
              <span>{{ item.name }}</span>
            </div>
          </template>
        </div>
        <div v-if="data.partsList[data.partsList.length - 1].name === '都没有'" class="submit ">
          <div @click="submitMore(true)">不清楚</div>
          <div @click="submitMore(false)">确定</div>
        </div>
        <div v-if="data.partsList[data.partsList.length - 1].name !== '都没有'"
             @click="submitMore(false)" class="submit2 ">
          确定
        </div>
      </div>

      <div v-if="data.status === 3" :class="data.isiOS == 'iOS'?'model-continue-low model-continue':'model-continue'">
        <div @click="continueTap(2)">继续问诊</div>
        <div @click="continueTap(1)">直接推荐科室</div>
      </div>

      <div :style="'padding: 0 0 15px 0'" v-if="data.status === 4" :class="data.isiOS == 'iOS'?'model-ok':'model-ok'">
        <div class="otherBg">
          <el-button :disabled="data.loadingAll" @click="willChild('是')" class="status4">是</el-button>
          <el-button :disabled="data.loadingAll" @click="willChild('否')" class="otherBtns">否</el-button>
        </div>
      </div>
    </van-popup>
    <!-- 弹出框2 -->
    <van-popup @close="cancel2" :closeable="data.modelStatus === 1" round v-model="data.show2" position="center">
      <div v-if="data.modelStatus === 0" class="retrun">
        <div class="retrun-img">
          <img src="../static/confim.svg">
        </div>
        <div class="retrun-txt">
          点击确认后，当前进度将丢失， 并将为您重新进行导诊
        </div>
        <div class="retrun-footer">
          <div @click="model2Calcel" class="rf-calcel">取消</div>
          <div @click="model2Ok" class="rf-ok">确定</div>
        </div>
      </div>

      <div v-if="data.modelStatus === 1" class="hospital">
        <div class="title-h">请选择院区</div>
        <div @click="goback" class="h-block">
          <img src="../static/hospital.png">
          <div class="txt-h">
            <div>上海市儿童医院（泸定路）</div>
            <div>三级甲等</div>
            <div>普陀区泸定路355号</div>
          </div>
        </div>
        <div @click="goback" class="h-block">
          <img src="../static/hospital.png">
          <div class="txt-h">
            <div>上海市儿童医院（北京西路）</div>
            <div>三级甲等</div>
            <div>静安路北京西路1400-24</div>
          </div>
        </div>
      </div>
    </van-popup>

    <select-hospital v-if="data.hospitalView" ref="endPath"></select-hospital>
  </div>
</template>

<script>
import wx from '../plugins/request'
import selectHospital from "../component/selectHospital";
import moment from 'moment'
import {Toast} from 'vant'

//测试环境api
// const baseUrl2 = "https://bs-test.digitalgd.com.cn/health-zndz/medical/graph/state/execute"
// const factUrl = "https://bs-test.digitalgd.com.cn/health-zndz/login?auth_request_code="

//生产环境
// const baseUrl2 = "https://www.gdzwfw.gov.cn/health-zndz/medical/graph/state/execute"
// const factUrl = "https://www.gdzwfw.gov.cn/health-zndz/login?auth_request_code="

//线下环境
// const baseUrl2 = "https://debugcheckdirect-beta.talkinggenie.com/medical-graph/state/execute"
const factUrl = "http://www.fghhhh.gov.cn/health-zndz/login?auth_request_code="
const baseUrl2 = "https://medical.beta.duiopen.com/state/execute"
let  windowHeight = undefined

export default {
  name: 'App',
  components: {
    selectHospital
  },
  data() {
    return {
      data: {
        viewHeight: 0,
        viewLeft: 0,
        viewRight: 0,
        testSign: 0,
        scrollTop: 0,
        clientWidth: 0,
        systemHeight: 150,
        isiOS: false,
        initUser: true,
        hospitalView: false,
        bottomHeight: 0,
        //定位
        position: {},
        //按住说话位置
        startY: 0,
        //弹框1
        show: true,
        //弹框2
        show2: false,
        loading: false,
        //阻止重复点击
        loadingAll: false,
        modelStatus: 1,
        showSex: false,
        selectName: '请选择就诊对象',
        //是否继续问诊
        continueSign: false,
        sumChina: ['一', '二', '三', '四', '五', '六'],
        //用户信息
        userInfo: null,
        age: 0,
        sex: '',
        isKey: true,
        vioceStatus: false,
        dynamicImg: '',
        timer: null,
        //其中isup为是否点赞
        msgList: [],
        msgValue: '',
        sexAndAge: [
          {
            values: ['男', '女'],
            className: 'sex',
            defaultIndex: 0
          },
          {
            values: ["0-28天", "29天-6岁", "7岁-15岁", "16岁-35岁", "36岁-60岁", "大于60岁"],
            className: 'index',
            defaultIndex: 3
          }
        ],
        //会话状态 0初始值 1、2、3 流程中 4推荐科室
        process: 0,
        //不可以重复点击
        noClike: false,
        //翻译中
        fanyi: false,
        //声音是否授权
        isRecord: true,
        //公众号分享过来的信息
        localMsg: [],
        columnsSign: true,
        //单选的列
        oneColumns: [],
        //发消息的状态
        sendStatus: false,
        //copy localmsg
        nameColumns: [],
        //弹框状态 1：对象 2：部位 3：继续问诊否
        status: 1,
        selectList: [],
        //翻译中
        overlayShow: false,
        //流程时间
        sessionTime: 0,
        //params
        params: {
          query: '',
          sessionId: "wx162883916349",
          machineId: "210000000000003"
        },
        //部位/身体
        partsList: [],
        //三秒后消息提示消
        toolmsg: true
      }
    }
  },
  mounted() {
    this.$getLocationInfo((result) => {
      console.info(result)
      this.data.position = result.position
    })
    //test
    setTimeout(() => {
      // let obj = document.getElementById("inputs")
      // console.log(obj)
      // document.getElementById("inputs").onkeydown = () =>  {
      //   window.scroll("0,0");
      //   this.setData({
      //     viewHeight: 0
      //   });
      // }
      // console.log(obj.onfocus)
      windowHeight = window.innerHeight
      console.log(windowHeight)
    }, 200)
    //小程序传过来的code
    let code = this.$route.query.code
    console.log(code, 'router')
    this.pushMsg('请问您最近哪里不舒服呢，可以告诉我，我将为您推荐可挂号的科室', 0)
    //初始化参数
    this.data.params.sessionId = this.guid() + new Date().getTime();
    setTimeout(() => {
      this.setData({
        toolmsg: false
      })
    }, 3000)
    //初始化用户
    //公众号传过来的信息
    // this.data.loadingAll =  true
    // wx.request({
    //   url: factUrl + code,
    //   method: 'get',
    //   success: (res) => {
    //     console.log(res, 'deafult')
    //     this.data.loadingAll =  false
    //     let data = res.data.data
    //     let sex = data.gender
    //     let card = data.certificateNo || ''
    //     if (sex === 1) {
    //       this.data.sexAndAge[0].defaultIndex = 0
    //     }else if(sex === 2){
    //       this.data.sexAndAge[0].defaultIndex = 1
    //     }
    //     this.getTime(card);
    //     this.setData({
    //       localMsg: JSON.parse(JSON.stringify(this.data.sexAndAge))
    //     })
    //     this.data.nameColumns = JSON.parse(JSON.stringify(this.data.localMsg))
    //   },
    //   fail: (e) => {
    //     this.data.loadingAll =  false
    //     this.data.sexAndAge[0].defaultIndex = 0
    //     this.setData({
    //       localMsg: JSON.parse(JSON.stringify(this.data.sexAndAge))
    //     })
    //     this.data.nameColumns = JSON.parse(JSON.stringify(this.data.localMsg))
    //     console.log(e, 'err')
    //   }
    // })
    this.data.loadingAll =  false
    this.data.sexAndAge[0].defaultIndex = 0
    this.setData({
      localMsg: JSON.parse(JSON.stringify(this.data.sexAndAge))
    })
    this.data.nameColumns = JSON.parse(JSON.stringify(this.data.localMsg))
    // console.log(e, 'err')
    // this.setFirst();
    let body = document.getElementById('home')
    let obj = {screenHeight: body.clientHeight}
    this.data.clientWidth = body.clientWidth
    console.log(obj)

    this.setData({
      isiOS: obj.screenHeight >= 600 ? 'iOS' : false
    })

    setTimeout(() => {
      console.log(this.data.isiOS, 'dsd')
    })

    //如果会话过期则重新开始会话
    setInterval(() => {
      this.data.sessionTime++;
      if (this.data.sessionTime > 60 * 30) {
        this.setData({
          show: false,
          show2: false,
          columnsSign: true,
          process: 0
        })
        this.data.sessionTime = 0;
        // if(this.data.msgList.length > 0 && this.data.msgList[this.data.msgList.length - 1].type != 2 && this.data.msgList[this.data.msgList.length - 1].msg != "请问您最近哪里不舒服呢，可以告诉我，我将为您推荐可挂号的科室" && this.data.msgList[this.data.msgList.length - 1].msg != "请描述一下您的症状？"){
        this.pushMsg('会话已过期', 0);
        this.model2Ok();
        // }
      }
    }, 1000)
  },
  methods: {
    //计算日期
    getTime(card) {
      card += ''
      if (card.length === 0) return
      let time = card.substring(6, 10) + '-' + card.substring(10, 12) + '-' + card.substring(12, 14)
      console.log(time)
      let m1 = moment()
      let m2 = moment(time)
      let day = m1.diff(m2, 'day')
      console.log(day, '天数')
      //闰年数
      let sumd = 0
      let timeArr = []
      for (let i = m2.year(); i <= m1.year(); i++) {
        if (i % 100 === 0) {
          if (i % 4 === 0) {
            sumd++
          }
        } else if (i % 4 === 0) {
          sumd++
        }
        timeArr.push(sumd)
      }
      console.log(sumd, timeArr, '天数d')
      if (day <= 28) {
        this.data.sexAndAge[1].defaultIndex = 0
      } else if (day <= 365 * 6 + (timeArr.length >= 6 ? timeArr[5] : sumd)) {
        this.data.sexAndAge[1].defaultIndex = 1
      } else if (day <= 365 * 15 + (timeArr.length >= 15 ? timeArr[14] : sumd)) {
        this.data.sexAndAge[1].defaultIndex = 2
      } else if (day <= 365 * 35 + (timeArr.length >= 35 ? timeArr[34] : sumd)) {
        this.data.sexAndAge[1].defaultIndex = 3
      } else if (day <= 365 * 60 + (timeArr.length >= 60 ? timeArr[59] : sumd)) {
        this.data.sexAndAge[1].defaultIndex = 4
      } else if (day > 365 * 60) {
        this.data.sexAndAge[1].defaultIndex = 5
      }
      console.log(this.data.sexAndAge[1].defaultIndex, this.data.sexAndAge[1].values, '最终选择')
    },

    toFooter() {
      let id = document.getElementById('contain')
      id.scrollTop = id.scrollHeight
    },

    //小程序方法转h5
    setData(obj) {
      for (let i in obj) {
        if (i == "scrollTop") {
          setTimeout(() => {
            this.toFooter()
          }, 200)
        }
        if (this.data[i] !== undefined) {
          this.$set(this.data, i, obj[i])
          // this.data[i] = obj[i];
        }
      }
    },

    //初始化接口
    setFirst() {
      let type = 1;
      this.setData({
        loadingAll: true
      })
      if (type) {
        this.data.params.query = '开启';
        wx.request({
          url: baseUrl2,
          method: "post",
          data: this.data.params,
          success: (res) => {
            console.log('开启对话');
            this.secondStep(this.data.localMsg[0].sex, this.data.localMsg[0].age, true);
            this.data.sex = this.data.localMsg[0].sex;
            this.data.age = this.data.localMsg[0].age;
            let str = this.data.localMsg[0].text + ' | ' + this.data.localMsg[0].sex + ' | ' + this.data.localMsg[0].age;
            this.setData({
              selectName: str
            })
          },
          fail: (res) => {
            console.log(res, '失败')
          }
        })
      } else {
        this.setData({
          show: true,
          localMsg: JSON.parse(JSON.stringify(this.data.sexAndAge))
        })
      }
    },

    //生成唯一值
    guid() {
      return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function (c) {
        var r = Math.random() * 16 | 0,
            v = c == 'x' ? r : (r & 0x3 | 0x8);
        return v.toString(16);
      })
    },

    //授权
    getUserProfile() {
      return new Promise((resolve) => resolve());
    },

    //弹框2关闭
    cancel2() {
      this.setData({
        show2: false
      });
    },

    //挂号
    registered(name) {
      this.data.hospitalView = true
      // this.position = {}
      this.$nextTick(() => {
        this.$refs.endPath.init(this.data.position.lng || '', this.data.position.lat || '',  name)
      })
    },

    //点赞
    setUporDown(signs, type) {
      let sign = type;
      let index = signs;
      this.data.msgList[index].isup = sign == 1 ? true : false;
      this.setData({
        msgList: this.data.msgList
      })
    },

    //初始化对话
    init() {
      //开启一轮新的对话
      this.data.params.query = '开启';
      wx.request({
        url: baseUrl2,
        method: "post",
        data: this.data.params,
        success: (res) => {
          console.log('开启对话');
        },
        fail: (res) => {
          console.log(res, '失败')
        }
      })
    },

    //返回公众号
    goback() {
      this.setData({
        show2: false
      })
      // Notify({
      //   type: 'success',
      //   message: '选择成功'
      // });
    },

    //消息框焦点获取与失去
    inputFocus(even) {
      let height = even.detail.height;
      console.log(height, '高度');
      this.setData({
        viewHeight: height,
      });
      setTimeout(() => {
        this.setData({
          bottomHeight: height + 180,
        });
        this.setData({
          scrollTop: 100000
        })
      }, 100)
    },

    inputblur() {
      this.setData({
        viewHeight: 0
      })
      this.orIos()
    },

    //ios input兼容
    orIos(){
      let windowFocusHeight = window.innerHeight
      if (windowHeight == windowFocusHeight) {
        return
      }
      console.log(windowHeight + '--' + windowFocusHeight);
      console.log('修复');
      let currentPosition;
      let speed = 1; //页面滚动距离
      currentPosition = document.documentElement.scrollTop || document.body.scrollTop;
      currentPosition -= speed;
      window.scrollTo(0, currentPosition); //页面向上滚动
      currentPosition += speed; //speed变量
      window.scrollTo(0, currentPosition); //页面向下滚动
    },

    //消息动画效果
    pushMsg(msg, sign) {
      this.setData({
        msgValue: ''
      });
      this.data.msgList.push({
        msg: msg,
        type: sign
      });
      this.setData({
        msgList: this.data.msgList,
        viewLeft: -400,
        viewRight: -400,
        sendStatus: true
      });
      setTimeout(() => {
        //设置一个滚动最大值
        this.setData({
          scrollTop: 100000,
          viewLeft: 0,
          viewRight: 0,
          sendStatus: true
        });
        setTimeout(() => {
          this.$set(this.data, 'viewLeft', 0)
        }, 300)
      }, 100)
    },

    //选择器1
    onClose() {
      if (this.data.selectName == '请选择就诊对象' || this.data.selectName == '请重新选择就诊对象' || this.data.columnsSign) {
        this.setData({
          show: false,
          loading: false,
          localMsg: JSON.parse(JSON.stringify(this.data.nameColumns))
        })
      } else {
        // Notify({
        //   type: 'warning',
        //   message: '流程中不允许取消'
        // });
        Toast('流程中不允许取消');
      }
    },

    //弹出弹框
    showModel() {
      //初始状态不弹窗
      if (this.data.process <= 1 && this.data.show && this.data.selectName === '请选择就诊对象') {
        return;
      }
      //有弹框的情况无法切换用户
      if (this.data.process <= 1 && (this.data.show || this.data.show2)) {
        // Notify({
        //   type: 'warning',
        //   message: '请点击重新导诊'
        // });
        if (this.data.status === 4) Toast('请先选择选项');
        else Toast('请点击重新导诊');
        return;
      }
      this.inputblur();
      console.log(this.data.process);
      if (this.data.process > 1 && this.data.process != 4) {
        // Notify({
        //   type: 'warning',
        //   message: '请点击重新导诊'
        // });
        Toast('请点击重新导诊');
        return; // 流程不可逆转
      }
      this.setData({
        show: true,
        columnsSign: true,
        status: 1
      })
      setTimeout(() => {
        let obj = document.getElementById('picker')
        console.log(obj.clientHeight, obj.offsetHeight)
        this.setData({
          bottomHeight: obj.clientHeight + (this.data.isiOS == 'iOS' ? 150 : 100),
        })
        setTimeout(() => {
          this.toFooter()
        })
      }, 200)
    },

    //单选确定按钮
    submitOne() {
      let picker = this.$refs.picker;
      let value = picker.getValues()
      console.log(value)
      this.onConfirm(value[0].text);
    },

    //单多选按钮选择
    moreSelect(index) {
      //danxuan
      if (this.data.partsList[index].one) {
        this.data.partsList.forEach(e => {
          e.select = false;
        });
        this.data.partsList[index].select = true;
        return;
      }
      //duoxuan
      if (this.data.partsList[index].sign && !this.data.partsList[index].select) {
        this.data.partsList.forEach(e => {
          e.select = false;
        });
        this.data.partsList[index].select = true;
      } else {
        this.data.partsList.forEach(e => {
          if (e.sign) e.select = false;
        });
        this.data.partsList[index]['select'] = !this.data.partsList[index]['select'];
      }
      this.setData({
        partsList: this.data.partsList
      })
    },

    //多选提交
    submitMore(sign) {
      try {
        if (this.data.loadingAll) return false;
        let str = '';
        this.data.partsList.forEach(e => {
          if (e.select) str += e.name + ',';
        });
        if (sign) str = '不清楚、';
        if (str.length === 0) {
          // Notify({ type: 'warning', message: '未选择任何内容' });
          Toast('未选择任何内容');
          return;
        }
        if (str.length > 0) str = str.substring(0, str.length - 1);
        this.pushMsg(str, 1);
        this.setData({
          show: false
        })
        this.data.params.query = str;
        this.setData({
          loading: true
        })
        this.data.loadingAll = true;
        //api处理返回
        wx.request({
          url: baseUrl2,
          method: "post",
          data: this.data.params,
          success: (res) => {
            if (res.data.message) {
              this.data.loading = false;
              Toast.fail('页面开小差，请重新导诊');
            }
            this.setData({
              loading: false
            })
            this.data.loadingAll = false;
            this.data.status = 0;
            this.fourthPart(res);
          },
          fail: (res) => {
            this.setErrors();
            this.data.loadingAll = false;
            this.setData({
              loading: false
            })
            console.log(res, 'more失败')
          }
        })
      } catch (error) {
        this.data.loadingAll = false;
        console.log(error);
        this.data.loading = false;
        Toast.fail('页面开小差，请重新导诊');
        this.setData({
          show2: false,
          show: false
        })
      }

    },

    //从新导诊
    refresh() {
      if (this.data.selectName === '请选择就诊对象') {
        // Toast('请先选择就诊对象');
        return;
      }
      if (this.data.msgList.length <= 1) {
        Toast('导诊已更新');
      }
      this.inputblur();
      this.setData({
        modelStatus: 0,
        show2: true
      })
    },

    model2Calcel() {
      this.setData({
        show2: false
      })
    },

    //重新导诊确定
    model2Ok() {
      this.data.sessionTime = 0; //时间归零
      this.data.params.sessionId = this.guid() + new Date().getTime();
      this.setData({
        show2: false,
        show: false,
        process: 0,
        columnsSign: true,
        status: 1
      })
      this.data.params.query = '开启';
      wx.request({
        url: baseUrl2,
        method: "post",
        data: this.data.params,
        success: (res) => {
          this.secondStep(this.data.sex, this.data.age)
        },
        fail: (res) => {
          console.log(res, '失败')
        }
      })
    },

    msgSelectTwo() {
      this.msgSelect();
    },

    //表单 也是第一步选择 或者单选
    onConfirm(value, index) {
      try {
        let picker = this.$refs.picker;
        value = picker.getValues();
        this.data.initUser = false;
        this.setData({
          loadingAll: true
        })
        if (!this.data.columnsSign) {
          this.pushMsg(value, 1);
          this.data.params.query = value;
          wx.request({
            url: baseUrl2,
            method: "post",
            data: this.data.params,
            success: (res) => {
              //处理错误信息
              if (res.data.message) {
                this.data.loading = false;
                Toast.fail('页面开小差，请重新导诊');
              }
              this.setData({
                loading: false,
                loadingAll: false
              })
              this.fourthPart(res);
            },
            fail: (res) => {
              this.setErrors();
              console.log(res, 'msg失败')
              this.setData({
                loading: false,
                loadingAll: false
              })
            }
          })
        } else {
          //异步加载
          if (this.data.noClike) return;
          this.getUserProfile().then(() => {
            let name = '';
            //如果切换用户 需要切换对话id 判断是不是同一个
            if (this.data.localMsg[0].values && this.data.localMsg[0].values[0] === '男') {
              name = value[0] + '  |  ' + value[1];
            } else {
              name = value.text + " | " + value.sex + " | " + value.age;
            }
            //相同名直接关闭弹窗
            if (name == this.data.selectName) {
              this.setData({
                show: false,
                loadingAll: false,
                localMsg: JSON.parse(JSON.stringify(this.data.nameColumns))
              })
              return;
            }
            //设置加载
            this.setData({
              loading: true
            })
            //加载姓名 年龄
            if (value.text === '其他') {
              this.setData({
                loadingAll: false,
                loading: false,
                localMsg: JSON.parse(JSON.stringify(this.data.sexAndAge))
              })
            } else {
              //取name age
              if (this.data.localMsg[0].values && this.data.localMsg[0].values[0] === '男') {
                this.setData({
                  localMsg: JSON.parse(JSON.stringify(this.data.nameColumns)),
                  selectName: value[0] + '  |  ' + value[1]
                })
                //进行第二步
                this.data.age = value[1];
                this.data.sex = value[0];
              } else {
                this.setData({
                  selectName: value.text + " | " + value.sex + " | " + value.age
                })
                //进行第二步
                this.data.age = value.age;
                this.data.sex = value.sex;
              }
              this.setData({
                show: false,
                localMsg: JSON.parse(JSON.stringify(this.data.nameColumns))
              })
              //第一次取值 还是其他次
              if (this.data.selectName !== '请选择就诊对象') {
                this.data.params.sessionId = this.guid() + new Date().getTime();
                this.data.params.query = '开启';
                this.data.sessionTime = 0; //时间归零
                wx.request({
                  url: baseUrl2,
                  method: "post",
                  data: this.data.params,
                  success: (res) => {
                    console.log('开启对话');
                    this.setData({
                      loadingAll: false
                    })
                    this.secondStep(this.data.sex, this.data.age);
                  },
                  fail: (res) => {
                    this.setData({
                      loadingAll: false,
                      loading: false
                    })
                    console.log(res, '失败')
                  }
                })
              } else {
                this.secondStep(this.data.sex, this.data.age);
              }
            }
          })
        }
      } catch (error) {
        console.log(error, '!!');
        this.setErrors();
      }
      //不管结果都设为1
      this.setData({
        process: 1
      })
    },

    setErrors() {
      // Notify({
      //   type: 'danger',
      //   message: '出错了'
      // });
      this.data.loading = false;
      Toast.fail('页面开小差，请重新导诊');
      this.setData({
        show: false,
        show2: false,
        columnsSign: true
      })
    },

    //  询问症状 默认选择性别 年龄
    secondStep(sex, age, first) {
      this.data.params.query = sex;
      this.data.loadingAll = true;
      this.setData({
        loading: true
      })
      wx.request({
        url: baseUrl2,
        method: "post",
        data: this.data.params,
        success: (res) => {
          if (res.message) {
            this.setData({
              loading: false,
              loadingAll: false
            })
          }
          console.log(res, '性别');
          if (age.length > 1) this.data.params.query = this.selectAge(this.data.age);
          else this.data.params.query = age
          wx.request({
            url: baseUrl2,
            method: "post",
            data: this.data.params,
            success: (res) => {
              this.setData({
                loading: false,
                loadingAll: false
              })

              if (first && res.data) {
                this.pushMsg('请问您最近哪里不舒服呢，可以告诉我，我将为您推荐可挂号的科室', 0);
              } else {
                this.fourthPart(res);
              }
            },
            fail: (res) => {
              console.log(res, '失败2')
              this.setData({
                loading: false,
                loadingAll: false
              })
            }
          })
        },
        fail: (res) => {
          this.setData({
            loading: false,
            loadingAll: false
          })
          console.log(res, '失败1')
        }
      })
    },

    // 提交病症 || 提交框提交 || 重置会话
    msgSelect() {
      console.log(this.data.sex, this.data.age, '???')
      this.orIos()
      if (this.data.msgValue.length === 0) {
        // Notify({
        //   type: 'warning',
        //   message: '消息为空，不能发送'
        // });
        Toast('消息为空，不能发送');
        return;
      }
      //先一步赋值
      this.data.params.query = this.data.msgValue;
      if (this.data.selectName === '请选择就诊对象') {
        this.setData({
          msgValue: ''
        });
        // Notify({
        //   type: 'warning',
        //   message: '请先选择就诊对象'
        // });
        Toast('请先选择就诊对象');
        return;
      }
      this.pushMsg(this.data.msgValue, 1);
      //如果流程最后一步 重置流程
      if (this.data.process === 4) {
        this.data.params.sessionId = this.guid() + new Date().getTime();
        this.setData({
          process: 0
        });
        this.restart(this.data.params.query);
        return;
      }
      this.setData({
        loading: true
      })
      wx.request({
        url: baseUrl2,
        method: "post",
        data: this.data.params,
        success: (res) => {
          if (res.data.message) {
            // Notify({
            //   type: 'danger',
            //   message: '服务异常，请点击重新导诊'
            // });
            this.data.loading = false;
            Toast.fail('页面开小差，请重新导诊');
          }
          //延迟发送有发消息的感觉
          this.setData({
            loading: false,
          })
          setTimeout(() => {
            this.fourthPart(res);
          }, 500)
        },
        fail: (res) => {
          this.setData({
            loading: false
          })
          console.log(res, '失败')
        }
      })
    },

    // 选择病症或者部位 分几种情况 单选或者多选
    fourthPart(res, sign) {
      try {
        if (res.message) {
          this.data.loading = false;
        }
        let type = res.data.data.answer.CSSType
        let msg = res.data.data.answer
        this.data.sessionTime = 0; //时间归零
        //取消继续问诊的状态位
        this.setData({
          continueSign: false
        })
        //如果流程已经完成直接结束流程
        if(res.data.data.complete){
          this.data.process = 4
        }
        if (type == undefined) {
          this.pushMsg(msg.reply, 0);
          return;
        }
        //如果不出错则进去第二个阶段
        setTimeout(() => {
          if (type !== 0) {
            this.setData({
              process: 2
            });
          }
          switch (type) {
            case 0:
              if (this.data.msgList.length !== 1) this.pushMsg(msg.reply, 0);
              break;
            case 1:
              this.more(msg, 'one');
              break;
            case 2:
              this.more(msg, 'one');
              break;
            case 3:
              this.more(msg);
              break;
            case 5:
              this.more(msg, true);
              break;
            case 6:
              this.continue(msg);
              break;
            case 7:
              this.end(msg);
              break;
          }
        }, 200)

      } catch (e) {
        this.pushMsg('出错了，没有找到相关内容哦', 0);
        //回溯
        this.setData({
          process: 1,
          show: false,
          show2: false
        });
        console.log(e);
      }
    },

    end(msg) {
      setTimeout(() => {
        this.data.process = 4;
        this.command(msg);
      })
    },

    //单选 现在改为多选
    one(msg, sign) {
      this.data.oneColumns = [];
      this.pushMsg(msg.reply, 0);
      if (sign === 2) {
        let data = msg.units ? msg.units : msg.data ? msg.data.value : msg.value;
        data.forEach(e => {
          let one = {};
          if (sign === 2) one = {
            text: e.name ? e.name : e,
            value: '1'
          }
          else one = {
            text: e
          };
          this.data.oneColumns.push(one)
        });
      } else {
        let data = msg.data ? msg.data.value : msg.value;
        if (msg.data) {
          data.forEach(e => {
            let one = {
              text: e.name ? e.name : e
            };
            this.data.oneColumns.push(one)
          });
        } else if (msg.value) {
          data.forEach(e => {
            let one = {
              text: e.name ? e.name : e
            };
            this.data.oneColumns.push(one)
          });
        }
      }
      this.setData({
        oneColumns: this.data.oneColumns,
        columnsSign: false,
        show: true,
        status: 1
      })
      // 设置容器高度
      setTimeout(() => {
        let obj = document.getElementById('picker')
        console.log(obj.clientHeight, obj.offsetHeight)
        this.setData({
          bottomHeight: obj.clientHeight + (this.data.isiOS == 'iOS' ? 150 : 100),
        })
        this.setData({
          scrollTop: 100000
        })
      }, 100)
    },

    //是否的选择
    willChild(msg) {
      if (this.data.loadingAll) return;
      this.data.loadingAll = true;
      this.data.params.query = msg;
      this.pushMsg(msg, 1);
      wx.request({
        url: baseUrl2,
        method: "post",
        data: this.data.params,
        success: (res) => {
          if (res.data.message) {
            Toast.fail('页面开小差，请重新导诊');
          }
          this.data.show = false;
          this.data.loading = false;
          this.data.loadingAll = false
          this.data.status = 0;
          setTimeout(() => {
            this.fourthPart(res);
          }, 200)
        },
        fail: (res) => {
          this.setErrors();
          this.data.loadingAll = false;
          this.setData({
            show: false,
            loading: false
          })
          console.log(res, 'more失败')
        }
      })
    },

    //多选
    more(msg, sign) {
      let datas = msg.data ? msg.data.value : msg.value ? msg.value : [];
      this.pushMsg(msg.reply, 0);
      this.$forceUpdate();
      //是不是怀孕的判断
      if (datas.length === 2 && datas[0].name === '是') {
        this.data.show = true;
        this.data.status = 4;
        this.data.process = 1;
        return;
      }
      this.data.partsList = [];
      this.setData({
        partsList: [],
      })
      datas.forEach(e => {
        this.data.partsList.push({
          name: e,
          select: false,
          one: sign && sign === 'one'
        })
      });
      //排序一下
      this.data.partsList.sort((a, b) => a.name.length - b.name.length);
      //排序后再加都没有
      if (sign && sign !== 'one') {
        this.data.partsList.push({
          name: '都没有',
          sign: true,
          select: false
        });
      }
      this.setData({
        partsList: this.data.partsList,
        status: 2,
        modelScrollTop: 0,
        show: true
      });
      // 设置容器高度
      setTimeout(() => {
        let obj = document.getElementById('picker2')
        console.log(obj.clientHeight, obj.offsetHeight)
        this.setData({
          bottomHeight: obj.clientHeight + (this.data.isiOS == 'iOS' ? 150 : 100),
        })
        this.setData({
          scrollTop: 100000
        })
      }, 200)
    },

    //继续问诊
    continue(msg) {
      this.pushMsg(msg.reply, 0);
      this.setData({
        status: 3,
        show: true,
        bottomHeight: null,
        continueSign: true
      });
    },

    //继续问诊触发事件
    continueTap(type) {
      console.log(this.data.loadingAll, 'jixuwenzhen')
      if (this.data.loadingAll) return;
      this.data.params.query = type;
      if (this.data.params.query === 2) this.pushMsg('继续问诊', 1);
      if (this.data.params.query === 1) this.pushMsg('直接推荐科室', 1);
      this.setData({
        loading: true
      })
      this.data.loadingAll = true;
      wx.request({
        url: baseUrl2,
        method: "post",
        data: this.data.params,
        success: (res) => {
          setTimeout(() => {
            this.data.loadingAll = false;
          }, 300);
          this.setData({
            loading: false,
            show: e.currentTarget.dataset.sign == 2 ? true : false
          })
          setTimeout(() => {
            let query = wx.createSelectorQuery();
            query.select('#picker').boundingClientRect(rect => {
              this.setData({
                bottomHeight: rect.height + (this.data.isiOS == 'iOS' ? this.data.systemHeight : 100),
              })
              //分开设置
              this.setData({
                scrollTop: 100000
              })
              console.log(rect.height);
            }).exec();
          }, 100)
          this.fourthPart(res);
        },
        fail: (res) => {
          this.data.loadingAll = false;
          this.setData({
            loading: false
          })
          console.log(res, '失败')
        }
      })
    },

    //推荐科室
    command(msg) {
      let data = msg.data.value;
      console.log(Array.isArray(data));
      if (!Array.isArray(data)) data = [data];
      let one = {
        msg: '以下是为您推荐的科室信息:',
        type: 2,
        isup: null,
        list: data
      };
      setTimeout(() => {
        this.data.msgList.push(one);
        this.setData({
          msgList: this.data.msgList,
          show: false,
          process: 4,
          bottomHeight: null
        })
        this.setData({
          scrollTop: 100000
        })
      }, 200)
    },

    //年龄控制
    selectAge(str) {
      console.log(str)
      let sign = 0;
      this.data.nameColumns[1].values.forEach((e, index) => {
        if (e == str) sign = index;
      });
      return sign;
    },
    //重置流程
    restart(msg) {
      this.setData({
        loading: true
      })
      let one = () => {
        return new Promise((resolve) => {
          this.data.params.query = '开启';
          wx.request({
            url: baseUrl2,
            method: "post",
            data: this.data.params,
            success: (res) => {
              resolve(1);
            },
            fail: (res) => {
              this.setData({
                loading: false,
                show: false,
                show2: false
              })
              console.log(res, '失败')
            }
          })
        })
      };

      let two = () => {
        return new Promise((resolve) => {
          this.data.params.query = this.data.sex;
          wx.request({
            url: baseUrl2,
            method: "post",
            data: this.data.params,
            success: (res) => {
              resolve(2);
            }
          })
        })
      }

      let there = () => {
        return new Promise((resolve) => {
          if (this.data.age.length > 1) this.data.params.query = this.selectAge(this.data.age);
          else this.data.params.query = this.data.age
          wx.request({
            url: baseUrl2,
            method: "post",
            data: this.data.params,
            success: (res) => {
              resolve(3);
            }
          })
        })
      }

      let four = () => {
        return new Promise((resolve) => {
          this.data.params.query = msg;
          wx.request({
            url: baseUrl2,
            method: "post",
            data: this.data.params,
            success: (res) => {
              this.fourthPart(res);
              this.setData({
                loading: false,
                process: 0
              })
              resolve(4);
            },
            fail: (res) => {
              this.setData({
                loading: false
              })
              console.log(res, '失败')
            }
          })
        })
      }
      try {
        one().then((res) => {
          console.log(res)
          return two();
        }).then((res) => {
          console.log(res)
          return there();
        }).then((res) => {
          console.log(res)
          if (this.data.sex === '女' && (this.data.age === '16岁-35岁' || this.data.age === '36岁-60岁')) {
            this.data.params.query = '正确'
            wx.request({
              url: baseUrl2,
              method: "post",
              data: this.data.params,
              success: (res) => {
                return four();
              }
            });
          } else {
            return four();
          }
        }).then((res) => {
          console.log(res)
        })
      } catch (error) {
        this.setData({
          loading: false
        })
      }
    }
  }
}
</script>
<style>
@import "./home.css";
</style>
