<template>
    <div class="select-hospital-layout">
        <van-popup :style="isData === undefined?'background:rgba(0,0,0,0)':''" round v-model="show" position="center">
            <div v-if="isData" class="model-one">
                <div class="title flow">
                    <div>推荐就诊医院</div>
                    <img @click="close" src="../static/close2.png">
                </div>
                <div class="m-content">
                    <div v-for="(item, index) of list" :key="index + 'p'" class="block">
                        <div class="block-one" @click="toMap(item)">
                            <div>{{item.name || '--'}}</div>
                            <div>
                                <div>{{item.distance || '--'}}km</div>
                                <img src="../static/address.svg"/>
                            </div>
                        </div>
                        <div class="block-two">
                            <div v-for="(e, i) of item.departmentData" :key="i + 'pw'">
                                <span @click="toDept(e.registerUrl)">{{e.name}}</span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div v-if="isData === false" class="model-one">
                <div class="title">
                    <div>推荐就诊医院</div>
                    <img @click="close" src="../static/close2.png">
                </div>
                <div class="center">
                    <img src="../static/nodata.png">
                </div>
                <div class="footer">
                    您好，附近没有匹配的医院，您可通过粤健通的“预约挂号”功能或其他渠道选择医院进行挂号
                </div>
            </div>
            <img v-if="isData === undefined" class="loading-img" src="../static/loading.svg">
        </van-popup>
    </div>
</template>

<script>
    const url = "http://172.16.58.101:8040/aispeech/medical/hospital/recommend"
    import wx from '../plugins/request'
    import {Toast} from "vant"

    export default {
        name: "selectHospital",
        data() {
            return {
                show: true,
                isData: undefined,
                list: []
            }
        },
        // mounted() {
        //     this.init()
        // },
        methods: {
            init(lng, lat, dept) {
                lat = 23.148141
                lng = 113.256609
                this.show = true
                wx.request({
                    url: url + `?department=${dept}&latitude=${lat}&longitude=${lng}`,
                    methods: 'get',
                    success: (res) => {
                        // this.show = true
                        try {
                            this.isData = true
                            this.list = res.data.data.list
                            if (this.list.length === 0) this.isData = false
                        } catch (e) {
                            this.isData = false
                            Toast('获取医院数据失败')
                        }
                    },
                    fail: () => {
                        this.isData = false
                    }
                })
            },
            toDept(url){
               if(url) window.open(url)
            },
            close(){
              this.show = false
            },
            toMap(list) {
                let arr = []
                if(list.latitude && list.longitude){
                    arr = [list.longitude, list.latitude]
                }
                this.$router.push({path: '/map', query: {lng: arr, name: list.name, address: list.fullAddress}})
            },
        }
    }
</script>

<style lang="scss" scoped>
    .select-hospital-layout {
        width: content-box;
        .model-one {
            width: 90vw;
            max-height: 90vh;
            background: white;
            padding: 0 16px 16px;
            box-sizing: border-box;
            overflow: auto;

            .title {
                display: flex;
                align-items: flex-end;
                height: 35px;

                > div {
                    font-size: 16px;
                    font-weight: 600;
                }

                > img {
                    width: 18px;
                    height: 18px;
                    margin-left: auto;
                }
            }

            .flow {
                position: sticky;
                padding-bottom: 5px;
                top: 0;
                background: white;
            }

            .center {
                height: 200px;
                display: flex;
                justify-content: center;
                align-items: center;

                > img {
                    width: 200px;
                    height: 100px;
                }
            }

            .m-content {
                margin-top: 5px;

                .block {
                    padding: 20px 0;
                    border-bottom: 1px solid #DDDDDD;

                    .block-one {
                        display: flex;

                        > div:first-child {
                            font-size: 16px;
                            color: #01BF8E;
                            line-height: 24px;
                        }

                        > div:last-child {
                            margin-left: auto;
                            display: flex;
                            width: content-box;
                            margin-top: 6px;

                            > div:first-child {
                                font-size: 13px;
                                color: #2E3133;
                            }

                            img {
                                height: 13px;
                                width: 13px;
                                margin-left: 10px;
                            }
                        }
                    }

                    .block-two {
                        font-size: 15px;
                        color: #2E3133;
                        line-height: 24px;
                        margin-top: 10px;
                        display: grid;
                        grid-template-columns: 33.33% 33.33% 33.33%;
                        white-space: pre-wrap;
                        /*>div:last-child{*/
                        /*    text-align: right;*/
                        /*}*/
                        /*>div:nth-child(2){*/
                        /*    margin-top: 5px;*/
                        /*    text-align: center;*/
                        /*}*/
                    }
                }

            }

            .footer {
                font-size: 15px;
                color: #2E3133;
                line-height: 24px;
            }
        }

        .loading-img{
            width: 30px;
            height: 30px;
            animation: rotating 2s linear infinite;
        }
    }
</style>