import Vue from 'vue'
import VueRouter from 'vue-router'
Vue.use(VueRouter)

import home from '../view/home'
import map from '../component/mapComp'

const routes = [
    {
        name: 'home',
        path: '/',
        component: home
    },
    {
        name: 'map',
        path: '/map',
        component: map
    }
]

const router = new VueRouter({
    routes
})

export default router
